    -o-transition: transform 500ms ease-in-out 2000ms, visibility 20ms ease-out 2500ms;
    transition: transform 500ms ease-in-out 2000ms, visibility 20ms ease-out 2500ms;
    transition: transform 500ms ease-in-out 2000ms, visibility 20ms ease-out 2500ms, -webkit-transform 500ms ease-in-out 2000ms;
  
