    }
    75% {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg)
    }
    100% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0)
    }
}

@-webkit-keyframes rightboxchange {
    0% {
        -webkit-transform: translate(0, 0);
